import React, { Component, Suspense } from 'react'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import './scss/style.scss'
import './style.css'
import ProtectedRoutes from './components/ProtectedRoutes'
import Reject from './views/reject/Index'
const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

// Containers
const DefaultLayout = React.lazy(() => import('./layout/DefaultLayout'))

// Pages
const Login = React.lazy(() => import('./views/pages/login/Login'))
const Pending = React.lazy(() => import('./views/pending/Index'))
const PendingUser = React.lazy(() => import('./views/pending/Pending'))
const Rejected = React.lazy(() => import('./views/reject/Index'))

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <Suspense fallback={loading}>
          <Routes>
            <Route
              exact
              path="/login"
              name="Login Page"
              element={
                <ProtectedRoutes>
                  <Login />
                </ProtectedRoutes>
              }
            />
            <Route path="*" name="Home" element={<DefaultLayout />} />
            <Route
              path="/rejected"
              name="Rejected"
              exact={true}
              element={
                <ProtectedRoutes>
                  <Rejected />
                </ProtectedRoutes>
              }
            /> <Route
              path="/pending"
              name="Pending"
              exact={true}
              element={
                <ProtectedRoutes>
                  <Pending />
                </ProtectedRoutes>
              }
            />
            <Route
              path="/pendingUser"
              name="Pending"
              exact={true}
              element={
                <ProtectedRoutes>
                  <PendingUser />
                </ProtectedRoutes>
              }
            />
            <Route
              path="/reject"
              name="Reject"
              element={
                <ProtectedRoutes>
                  <Reject />
                </ProtectedRoutes>
              }
            />
          </Routes>
        </Suspense>
      </BrowserRouter>
    )
  }
}

export default App
