import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import { Button } from 'react-bootstrap'
import '../../style.css'
import { userSlice } from '../../features/userSlice'
import { partnersSlice } from '../../features/partnersSlice'

const Index = () => {
  const user = useSelector((state) => state.user.user)
  const navigate = useNavigate()
  const dispatch = useDispatch()
  return (
    <div>
      <div
        style={{ height: '100vh', backgroundColor: '#072541' }}
        className="text-white d-flex flex-column"
      >
        <nav
          className="w-100 bg-black h1 text-white d-flex h-100 align-items-center justify-content-center text-center text-md-start"
          style={{ minHeight: '5rem', maxHeight: '7rem' }}
        >
          Sorry&nbsp; <i className="text-danger">&quot;{user && user.name}&quot;&nbsp;</i> You
          Account is &nbsp;<span className="text-danger"> rejected.</span>
        </nav>
        <main className="h-100 text-center fs-3 d-flex  flex-column justify-content-center">
          <p>Your Account does not meet with our policies</p>
          <p>For more information please contact us</p>
          <p>Thank You</p>
          <div className>
            <Button
              onClick={() => {
                navigate('/login')
                localStorage.clear()
                dispatch(userSlice.actions.clearUser())
                dispatch(partnersSlice.actions.clearPartners())
              }}
              className="myCustomBtn p-3"
            >
              Logout
            </Button>
          </div>
          <div className="mt-3 ">
            <Link
              className="text-decoration-none text-white"
              to={'https://adinath.site/contact-us/'}
            >
              Contact Us
            </Link>
          </div>
        </main>
        <footer className="bg-black justify-self-end">
          <div className="container">
            <div className="row">
              <div className="col-12 text-center text-white py-3">
                <p className="mb-0">
                  <Link className='text-info' to={'https://adinath.site/'}>Adinath Group Online</Link>© 2023 Adinath
                  Group.
                </p>
              </div>
            </div>
          </div>
        </footer>
      </div>
    </div>
  )
}

export default Index
